import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Header from '@vkontakte/vkui/dist/components/Header/Header';
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs';

import persik from '../img/persik.png';
import './Persik.css';

const osName = platform();

const Persik = props => (
	class Example extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      activeTab: 'groups'
    }
  }

  render() {
    return (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="home">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}
		>
			О проекте
		</PanelHeader>
		<Header mode="secondary">О проекте</Header>
		<Div style={{ color: 'gray' }}>
           DESTROID — паблик с халявой и мини-играми от бывших администраторов DINOCODE, VK Point, WW Play, FCoin.
Мы учли ошибки всех проектов и решили создать свой, приближенный к идеальному. Заходи, подписывайся, играй! У нас много интересного.
          </Div>
             <Tabs>
              <TabsItem
                selected={this.state.activeTab === 'groups'}
                onClick={() => this.setState({ activeTab: 'groups' })}
              >Лента</TabsItem>
              <TabsItem
                selected={this.state.activeTab === 'events'}
                onClick={() => this.setState({ activeTab: 'events' })}
              >Мини-игры</TabsItem>
            </Tabs>
             {this.state.activeTab === 'events' ? <Div>ъеъ</Div> : <Div>ъеееееъ</Div>}
	<Example />
	</Panel>
 )
  }
}
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;
